.header {
  &__content {
    box-sizing: content-box;
    height: 52px;
    padding: 20px 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    transition: 0.4s;
    z-index: 15;
    background: #ffffff;
    &.mobile {
      background: $yellow-dark;
    }
    &.hide {
      transform: translateY(-92px);
      transition: 0.4s;
    }

    @include width-960 {
      height: 44px;
    }
  }
  &__container {
    height: 100%;
  }
  &__body {
    display: flex;
    justify-content: space-between;
    height: 100%;
  }
  &__logo {
    flex-shrink: 0;
    margin-right: 50px;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    background: #000000;
    padding: 9px 15px;
    color: #ffffff;
    font-weight: 400;
    font-size: 26px;
    line-height: 130%;
    position: relative;
    z-index: 11;
    & svg {
      height: 28px;
      width: 40px;
      margin-right: 10px;
      fill: #FFE457;
    }

    @include width-960 {
      font-size: 24px;
      & svg {
        margin-right: 8px;
        width: 36px;
        height: 25.2px;
      }
    }
    @include width-640 {
      font-size: 22px;
    }
  }
  &__menu {
    display: flex;
    align-items: center;

    @include width-960 {
      display: none;
    }
  }
  &__list {
    display: flex;
  }
  &__link {
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
    color: #000000;
    text-decoration-line: underline;
    transition: 0.2s;
    &:not(:last-child) {
      margin-right: 40px;
    }
    &:last-child {
      margin-right: 50px;
    }
    &:hover {
      color: $yellow-dark;
      transition: 0.2s;
    }
  }
  &__btns {
    display: flex;
  }
  &__btn {
    flex-shrink: 0;
    position: relative;
    overflow: hidden;
    z-index: 2;
    height: 100%;
    border: 2px solid #000000;
    border-radius: 40px;
    background: none;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 20px;
    line-height: 110%;
    color: #000000;
    cursor: pointer;
    &::before {
      content: "";
      position: absolute;
      z-index: -1;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      clip-path: inset(100% 0 0 0);
      transition: 0.2s linear;
    }
    &:hover {
      &::before {
        clip-path: inset(0 0 0 0);
      }
    }
    &-app {
      padding: 15px 20px;
      &::before {
        background: $yellow-dark;
      }
    }
    &-telegram {
      border: none;
      background: none;
      cursor: pointer;
      padding: 0;
      margin-left: 20px;
      width: 52px;
      height: 52px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: $yellow-dark;
      border: 2px solid #000000;
      border-radius: 50%;
      &::before {
        background: #ffffff;
      }
      & svg {
        width: 25px;
        height: 21px;
        margin-left: -5px;
      }

      @include width-960 {
        height: 44px;
        width: 44px;
      }
    }
  }
  &__burger {
    border: none;
    background: none;
    cursor: pointer;
    position: relative;
    width: 52px;
    height: 44px;
    background: $yellow-dark;
    border: 2px solid #000000;
    flex-shrink: 0;
    display: none;
    &.burger--active {
      & span {
        transform: rotate(45deg);
        opacity: 0;
      }
      &::before {
        transform: rotate(45deg);
        bottom: 19px;
      }
      &::after {
        top: 20px;
        transform: rotate(-45deg);
      }
    }
    & span {
      left: 0;
      right: 0;
      top: 19px;
      margin: 0 auto;
      position: absolute;
      height: 1.5px;
      width: 28px;
      transition: all 0.3s ease 0s;
      background-color: #000000;
    }
    &::after {
      content: '';
      left: 0;
      right: 0;
      top: 14px;
      margin: 0 auto;
      position: absolute;
      height: 1.5px;
      width: 28px;
      transition: all 0.3s ease 0s;
      background-color: #000000;
    }
    &::before {
      content: '';
      left: 0;
      right: 0;
      bottom: 14px;
      margin: 0 auto;
      position: absolute;
      height: 1.5px;
      width: 28px;
      transition: all 0.3s ease 0s;
      background-color: #000000;
    }

    @include width-960 {
      display: block;
    }
  }
}

.header-mobile-menu {
  position: fixed;
  z-index: 10;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: $yellow-dark;
  overflow-y: scroll;
  transform: translateX(-100%);
  transition: 0.5s;
  &.menu--active {
    transform: translateX(0);
    transition: 0.5s;
  }
  &__container {
    height: 100%;
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
  }
  &__nav {
    margin-top: 160px;
  }
  &__link {
    font-weight: 900;
    font-size: 74px;
    line-height: 110%;
    border-top: 3px solid #000000;
    padding: 25px 0;
    position: relative;
    &:last-child {
      border-bottom: 3px solid #000000;
    }
    & a {
      display: flex;
      align-items: center;
      height: 63px;
      & svg {
        height: 63px;
      }
    }

    @include width-640 {
      padding: 20px 0;
      & a {
        & svg {
          height: 50px;
        }
      }
    }
  }
  &__btn {
    padding: 15px;
    width: 100%;
    min-height: 50px;
    margin-top: 60px;
    position: relative;
    overflow: hidden;
    border: 2px solid #000000;
    border-radius: 40px;
    background: none;
    font-weight: 500;
    font-size: 20px;
    line-height: 110%;
    color: #000000;
    cursor: pointer;
  }
  &__footer {
    display: flex;
    justify-content: space-between;
    margin-top: auto;
    padding-top: 150px;
    padding-bottom: 20px;
  }
}
