button:focus-visible {
  outline: 1px solid #ffd500;
}

a:focus-visible {
  outline: 1px solid #ffd500;
}

.noscroll {
  overflow: hidden;
}

.btn-main {
  position: relative;
  width: fit-content;
  font-weight: 500;
  font-size: 20px;
  line-height: 110%;
  cursor: pointer;
  &:hover {
    & div {
      transition: 0.3s;
      top: 0;
      right: 0;
    }
  }
  & button {
    border: 2px solid #000000;
    border-radius: 40px;
    background: $yellow-dark;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #000000;
    padding: 15px 20px;
    position: relative;
    z-index: 2;
  }
  & div {
    width: 100%;
    height: 100%;
    position: absolute;
    top: -8px;
    right: -8px;
    background: #ffffff;
    border: 2px solid #000000;
    border-radius: 40px;
    padding: 15px 20px;
    z-index: 1;
    transition: 0.3s;
  }

  @include width-640 {
    font-size: clamp(14px,14px + (20 - 14) * ((100vw - 320px)/(640 - 320)),20px);
  }
}

.title {
  &-h2 {
    font-weight: 600;
    font-size: 44px;
    line-height: 110%;
    color: #000000;
    text-transform: uppercase;

    @include width-640 {
      font-size: clamp(28px,28px + (44 - 28) * ((100vw - 320px)/(640 - 320)),44px);
    }
  }
}
