/* ----------------- INTRO ----------------- */
.intro {
  margin-top: 120px;
  @include width-1250 {
    margin-top: 84px;
  }
  &__content {
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
    height: 460px;

    @include width-960 {
      flex-direction: column;
      height: auto;
    }
    @include width-768 {
      align-items: flex-start;
    }
  }
  &__text {
    position: relative;
    z-index: 2;
    max-width: 980px;

    @include width-1450 {
      max-width: 760px;
    }
    @include width-1250 {
      max-width: 630px;
    }
    @include width-960 {
      order: 2;
      max-width: 100%;
      margin-right: 0;
      margin-top: 8px;
    }
    @include width-768 {
      max-width: 480px;
    }
  }
  &__title {
    font-weight: 600;
    font-size: 58px;
    line-height: 110%;
    color: #000000;
    text-transform: uppercase;

    @include width-1450 {
      font-size: 48px;
    }
    @include width-1250 {
      font-size: 38px;
    }
    @include width-768 {
      font-size: 28px;
    }
    @include width-640 {
      font-size: clamp(22px,22px + (28 - 22) * ((100vw - 320px)/(640 - 320)),28px);
    }
  }
  &__btn {
    margin-top: 50px;
  }
  &__img {
    width: 100%;
    max-width: 620px;
    position: absolute;
    right: 0;

    @include width-1700 {
      right: -125px;
    }
    @include width-1450 {
      max-width: 560px;
      right: -130px;
    }
    @include width-1250 {
      max-width: 500px;
      bottom: -10px;
    }
    @include width-1100 {
      max-width: 500px;
    }
    @include width-960 {
      position: static;
      order: 1;
      align-self: flex-end;
      margin-right: -20px;
      max-width: 450px;
    }
    @include width-768 {
      max-width: 350px;
      margin-right: 50px;
    }
    @include width-640 {
      margin-right: -90px;
    }
    // @include width-390 {
    //   display: block;
    //   flex-shrink: 1;
    //   margin-right: 0;
    // }
  }
}
/* ----------------- /INTRO ----------------- */

/* ----------------- ABOUT ----------------- */
.about {
  margin-top: 160px;

  @include width-960 {
    margin-top: 120px;
  }
  @include width-768 {
    margin-top: 80px;
  }

  &__content {
  }

  &__title {
    max-width: 800px;
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 20px;
    margin-top: 40px;

    @include width-1250 {
      grid-template-columns: 1fr 1fr;
    }
    @include width-640 {
      gap: 10px;
    }
  }
}

.about-hat {
  display: flex;
  align-items: flex-end;
  margin-top: 40px;

  &__text {
    margin-left: 290px;
    font-weight: 500;
    font-size: 26px;
    line-height: 110%;
    color: #000000;
    max-width: 610px;

    @include width-1700 {
      margin-left: 210px;
    }
    @include width-1450 {
      margin-left: 180px;
    }
    @include width-1250 {
      font-size: 22px;
    }
    @include width-960 {
      margin-left: 0;
      font-size: 18px;
      max-width: 450px;
    }
    @include width-640 {
      @include scale(18,14)
    }
  }
  & img {
    width: 120px;
    height: 60px;
  }
  @include width-960 {
    margin-top: 20px;
    & img {
      display: none;
    }
  }
}

.about-card {
  background: #FFE457;
  border: 4px solid #000000;
  padding: 30px 45px 50px;

  @include width-1700 {
    padding: 30px 30px 50px;
  }
  @include width-1450 {
    padding: 30px;
  }
  @include width-640 {
    padding: 10px;
  }

  &__img {
    width: 100%;
    height: 170px;
    background: #000000;
    display: flex;
    align-items: center;
    justify-content: center;
    & img {
      max-width: 120px;
    }

    @include width-1450 {
      height: 150px;
    }
    @include width-640 {
      height: clamp(94px,94px + (150 - 80) * ((100vw - 320px)/(640 - 320)),150px);
      & img {
        max-width: clamp(80px,80px + (120 - 80) * ((100vw - 320px)/(640 - 320)),120px);
      }
    }
  }

  &__title {
    font-weight: 700;
    font-size: 34px;
    line-height: 41px;
    color: #000000;
    margin-top: 20px;
    line-height: 100%;

    @include width-1450 {
      font-size: 28px;
    }
    @include width-640 {
      @include scale(28,18)
    }
  }

  &__subtitle {
    display: block;
    font-weight: 500;
    font-size: 20px;
    line-height: 120%;
    color: #000000;
    margin-top: 5px;

    @include width-1450 {
      font-size: 18px;
    }
    @include width-640 {
      font-size: clamp(12px,12px + (18 - 12) * ((100vw - 320px)/(640 - 320)),18px);
    }
  }
}

/* ----------------- /ABOUT ----------------- */

/* ----------------- WORKS ----------------- */
.works {
  background: #FFE457;
  border: 3px solid #000000;
  border-radius: 40px;
  padding: 120px 0;
  margin-top: 160px;
  position: relative;

  @include width-768 {
    margin-top: 120px;
    border-radius: 20px;
  }
  @include width-640 {
    margin-top: 80px;
    padding: clamp(80px,80px + (120 - 80) * ((100vw - 320px)/(640 - 320)),120px) 0;
  }

  &__title {
    margin-bottom: 10px;
  }

  &__types {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 40px;
    & >:not(:last-child) {
      margin-right: 10px;
    }

    @include width-768 {
      flex-wrap: nowrap;
      overflow-x: scroll;
      margin-left: -20px;
      margin-right: -20px;
      padding-left: 20px;
      padding-right: 20px;
      /* Скрываем scrollbar для Chrome, Safari и Opera */
      &::-webkit-scrollbar {
        display: none;
      }
      /* Скрываем scrollbar для IE, Edge и Firefox */
      -ms-overflow-style: none;  /* IE и Edge */
      scrollbar-width: none;  /* Firefox */
    }
  }

  &__type {
    display: block;
    position: relative;
    overflow: hidden;
    border: none;
    box-shadow: inset 0px 0px 0 2px #000000;
    height: 100%;
    border-radius: 40px;
    background: none;
    padding: 15px 20px;
    margin-top: 10px;
    cursor: pointer;
    transition: 0.2s;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      clip-path: inset(100% 0 0 0);
      transition: 0.2s linear;
    }
    &:hover {
      & span {
        color: $yellow-dark;
        transition: 0.2s;
        &::before {
          border: 2px solid $yellow-dark;
        }
      }
      &::before {
        clip-path: inset(0 0 0 0);
        background: #000000;
      }
    }
    & span {
      display: block;
      font-weight: 500;
      font-size: 20px;
      line-height: 110%;
      color: #000000;
      position: relative;
      z-index: 2;
      padding-left: 18px;
      transition: 0.2s;
      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 8px;
        height: 8px;
        background: none;
        border: 2px solid #000000;
        border-radius: 50%;
        transition: 0.2s;
      }
    }

    @include width-768 {
      flex-shrink: 0;
    }
    @include width-640 {
      & span {
        @include scale(20,16)
      }
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
    row-gap: 40px;

    @include width-640 {
      grid-template-columns: 1fr;
    }
  }

  &__btn {
    margin: 0 auto;
    margin-top: 50px;
  }
}

.works-card {
  &__img {
    border: 3px solid #000000;
    overflow: hidden;
    & img {
      width: 100%;
      height: 100%;
      transition: 0.2s;
    }
    &:hover {
      & img {
        scale: 1.04;
        transition: 0.2s;
      }
    }
  }

  &__title {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: #000000;
    margin-top: 10px;

    @include width-640 {
      @include scale(20, 16)
    }
  }

  &__subtitle {
    font-weight: 500;
    font-size: 18px;
    line-height: 130%;
    color: #000000;

    @include width-640 {
      @include scale(18, 14)
    }
  }
}
/* ----------------- /WORKS ----------------- */

