.service-card {
  box-sizing: border-box;
  display: block;
  border: 4px solid #000000;
  padding: 40px;
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: 0.3s;
  position: relative;
  z-index: 2;
  overflow: hidden;
  background: #ffffff;
  transition: 0.5s;

  @include width-1700 {
    height: 300px;
  }
  @include width-1450 {
    padding: 20px;
    height: 256px;
  }
  @include width-768 {
    height: 286px;
  }
  @include width-640 {
    height: clamp(210px,210px + (286 - 210) * ((100vw - 320px)/(640 - 320)),286px);
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    clip-path: inset(100% 0 0 0);
    background: $yellow-dark;
    transition: 0.3s linear;
  }
  &:hover {
    &::before {
      clip-path: inset(0 0 0 0);
    }
  }
  &__title {
    font-weight: 600;
    font-size: 30px;
    line-height: 130%;
    color: #000000;

    @include width-1450 {
      font-size: 26px;
    }
    @include width-640 {
      @include scale(26,20)
    }
  }
  &__more {
    font-weight: 500;
    font-size: 20px;
    line-height: 110%;
    color: #000000;
    display: flex;
    align-self: flex-end;
    position: relative;
    padding-right: 21px;
    &::after {
      content: '';
      position: absolute;
      width: 16px;
      height: 16px;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto 0;
      background: url('../img/svg/more.svg') no-repeat;
      background-size: 16px;
    }

    @include width-640 {
      @include scale(20,16)
    }
  }
}
/* ----------------- /SERVICES ----------------- */
