html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  overflow-x: hidden;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  overflow-x: hidden;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  @include width-1700 {
    max-width: 1440px;
  }
  @include width-1450 {
    max-width: 1240px;
  }
  @include width-1250 {
    max-width: 960px;
  }
  @include width-960 {
    max-width: 720px;
  }
  @include width-640 {
    padding: 0 10px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.wrap {
  &-white {
    background: #FFFFFF;
    border: 3px solid #000000;
    border-bottom-left-radius: 40px;
    border-bottom-right-radius: 40px;
    margin-top: -40px;
    padding-top: 200px;
    padding-bottom: 160px;
    overflow: hidden;

    @include width-768 {
      border-radius: 20px;
      padding-top: 140px;
      padding-bottom: 120px;
    }
  }
}
